export default {
    declensionUnits(number, unitsName) {
        let declensionVariants = {
            '. ': ['. ', '. ', '. '],
            '': ['', '', ''],
            '': ['', '', ''],
            '': ['', '', ''],
            '': ['', '', ''],
            '': ['', '', ''],
            '': ['', '', ''],
            '': ['', '', ''],
            '': ['', '', ''],
            '': ['', '', ''],
            '': ['', '', ''],
            '  ': ['  ', '  ', '  '],
            '  ': ['  ', '  ', '  '],
            '  ': ['  ', '  ', '  '],
        };

        if (typeof declensionVariants[unitsName] == 'undefined') {
            return unitsName;
        }

        let unitVariants = declensionVariants[unitsName];
        let lastDigit = parseInt(number.toString().substr(-1));

        if (number <= 10 || number >= 20) {
            if (lastDigit === 1) {
                return unitVariants[0];
            }

            if (lastDigit < 5 && lastDigit > 1) {
                return unitVariants[1];
            }

            if (lastDigit >= 5 || lastDigit === 0) {
                return unitVariants[2];
            }
        }
        else {
            return unitVariants[2];
        }
    },
    getTimeInHours(duration, durationUnits) {
        let activeHoursInDay = 8;
        let activeDaysInWeek = 5;
        let activeDaysInMonth = 20;
        let time = duration;
        let units = durationUnits;
        let hoursCoefficient = {
            '. ': 45/60,
            '': activeHoursInDay,
            '': 1,
            '': 1,
            '': 4,
            '': 1/60,
            '': activeHoursInDay*activeDaysInMonth,
            '': activeHoursInDay*activeDaysInWeek
        };

        let timeInHours = time * hoursCoefficient[units];

        return timeInHours;
    },
    getTimeInDays(duration, durationUnits) {
        let activeHoursInDay = 8;
        return this.getTimeInHours(duration, durationUnits) / activeHoursInDay;
    },
    getTimeWithUnitsString(time, units) {
        return Math.ceil(time) + ' ' + this.declensionUnits(Math.ceil(time), units);
    },
    getHumanReadableTime(duration, durationUnits) {
        let activeDaysInWeek = 5;
        let activeDaysInMonth = 20;

        let timeInHours = this.getTimeInHours(duration, durationUnits);
        let timeInDays = this.getTimeInDays(duration, durationUnits);
        let timeInWeeks = timeInDays / activeDaysInWeek;
        let timeInMonths = timeInDays / activeDaysInMonth;
        let timeInYears = timeInMonths / 12;
        let humanReadable = this.getTimeWithUnitsString(timeInHours, '');

        if (timeInHours < 1) {
            humanReadable = ' 1 ';
        }

        if (timeInDays >= 1) {
            humanReadable = this.getTimeWithUnitsString(timeInDays, '');
        }

        if (timeInWeeks >= 1) {
            humanReadable = this.getTimeWithUnitsString(timeInWeeks, '');
        }

        if (timeInMonths >= 1) {
            humanReadable = this.getTimeWithUnitsString(timeInMonths, '');
        }

        if (timeInYears >= 1) {
            humanReadable = this.getTimeWithUnitsString(timeInYears, '');
        }

        return humanReadable;
    },
    formatNumber(number) {
        return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, "&nbsp;");
    },
    lcfirst(string) {
        return string.charAt(0).toLowerCase() + string.slice(1);
    },
    lcfirstJoin(array, separator) {
        let lcArray = array.map( (item, index) => index === 0 ? item : this.lcfirst(item) );
        return lcArray.join(separator);
    },

    skillsToObjectList(maybeKeyValueObjectOrStringList) {
        let isList = maybeKeyValueObjectOrStringList instanceof Array;
        let isObjectList = isList && maybeKeyValueObjectOrStringList[0] instanceof Object;
        let isStringList = isList && typeof(maybeKeyValueObjectOrStringList[0]) === 'string';

        if (!isList) {
            return false;
        }

        if (isObjectList) {
            return maybeKeyValueObjectOrStringList;
        }

        if (isStringList) {
            return maybeKeyValueObjectOrStringList.map( (skillName) => {
                return {
                    name: skillName,
                    level: 0
                };
            });
        }

        return Object.keys(maybeKeyValueObjectOrStringList, (skillName) => {
            let skillLevel = maybeKeyValueObjectOrStringList[skillName];

            return {
                name: skillName,
                level: skillLevel,
            }
        });
    },
    getReadableValue(value, enumList) {
        if (!enumList) {
            return value;
        }

        return enumList.reduce( (previousValue, enumValue) => {
            if (enumValue.code === value) {
                return enumValue.title;
            }

            return previousValue;
        }, value);
    }
}